body, html {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.container {
  text-align: center;
  color: white;
  background-image: url('data:image/jpeg;base64,'); /* Replace with your background image URL */
  background-size: cover;
  background-position: center;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

header {
  margin-top: 20px;
}

h1 {
  margin-bottom: 20px;
}

.navigation-links {
  display: flex;
  gap: 40px;
}

.nav-link {
  color: #61dafb;
  text-decoration: none;
  font-size: 20px;
}

.nav-link:hover {
  text-decoration: underline;
}

.main-content {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(255, 0, 191);
}

.contact-section {
  background: rgba(0, 0, 0, 0.6);
  padding: 20px;
  border-radius: 10px;
  width: 300px;
  margin-bottom: 20px;
}

.contact-section h2 {
  margin-top: 0;
}

footer {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 0;
}

.copyright-section {
  background: rgba(0, 0, 0, 0.6);
  width: 100%;
  padding: 10px;
}

.copyright-section p {
  margin: 0;
  color: #aaa;
}
